.buttons-div {
  display: flex;
  margin: 0 auto;
}

.icon {
  height: 15px;
}

.close-button {
  height: 24px;
  margin-bottom: -5px;
  margin-left: 290px;
  cursor: pointer;
  transition: transform 0.1s ease;
}

.close-button:active {
  transform: scale(0.95);
}

.budget-icon {
  height: 15px;
}

.label {
  font-size: 14px;
  margin-left: 8px;
}

.button {
  display: flex;
  align-items: center;
  margin-inline: 12px;
  margin-top: 40px;
  padding: 12px 20px;
  background-image: linear-gradient(to bottom right, #010755, #0b0e6f);
  color: white;
  border-color: transparent;
  border-radius: 15px;
  font-weight: bolder;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  cursor: pointer;
}

.login-button {
  display: flex;
  align-items: center;
  margin-inline: 15px;
  margin-top: 10px;
  padding: 8px 30px;
  background-color: white;
  color: darkblue;
  border-color: transparent;
  border-radius: 11px;
  font-weight: bolder;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  cursor: pointer;
}

.budget-button-div {
  display: flex;
  justify-content: center;
}

.budget-button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-inline: 12px;
  margin-top: 40px;
  padding: 18px 24px;
  background-image: linear-gradient(to bottom right, #010755, #0b0e6f);
  color: white;
  border-color: transparent;
  border-radius: 15px;
  font-weight: bolder;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  cursor: pointer;
}

button:active {
  transform: scale(0.95);
  background-color: darkblue;
}
